<template>
  <div id="app" class="container-fluid">
    <div class="row bg-dark" style="height: 100vh">
      <Calculator :currencies="currencies" />
    </div>
  </div>
</template>

<script>
import Calculator from "@/components/Calculator";

export default {
  name: "App",
  data() {
    return {
      currencies: {
        USDPEN: { value: 3.8898, symbol: "PEN=X" },
        USDEUR: { value: 0.8429, symbol: "EUR=X" },
        PENUSD: { value: 0.2570826, symbol: "PENUSD=X" },
        PENEUR: { value: 0.2117, symbol: "PENEUR=X" },
      },
    };
  },
  created() {
    window.addEventListener("update-currencies", (e) => {
      for (let k in e.detail.data) this.currencies[k] = e.detail.data[k];
    });
  },
  components: { Calculator },
};
</script>

