<template>
  <div id="calculator" class="col-12">
    <div class="row">
      <h3>{{ title }}</h3>
      <div class="col-6 col-sm-4">
        <div class="input-group">
          <span class="input-group-text">Operaci&oacute;n</span>
          <input
            @click.self="clickHandler"
            type="text"
            class="form-control form-control-sm"
            v-model="b5"
          />
        </div>
      </div>
      <div class="col-12 mt-3">
        <table class="table table-sm table-light table-bordered">
          <thead>
            <tr class="text-center">
              <th>Casa</th>
              <th>Tasa</th>
              <th>Dolar</th>
              <th>Soles</th>
              <th>Euro</th>
            </tr>
          </thead>
          <tbody class="text-center">
            <tr>
              <td>#1</td>
              <td>
                <input
                  @click.self="clickHandler"
                  @keypress.enter="focusNextRate"
                  type="text"
                  class="form-control form-control-sm mx-auto text-center"
                  v-model.lazy="b3"
                />
              </td>
              <td>{{ c3().toFixed(2) }} &dollar;</td>
              <td>{{ (c3() * currencies.USDPEN.value).toFixed(0) }}</td>
              <td>{{ (c3() * currencies.USDEUR.value).toFixed(1) }}</td>
            </tr>
            <tr>
              <td>#2</td>
              <td>
                <input
                  id="rate-2"
                  @click.self="clickHandler"
                  type="text"
                  class="form-control form-control-sm mx-auto text-center"
                  v-model.lazy="b4"
                />
              </td>
              <td>{{ c4().toFixed(2) }} &dollar;</td>
              <td>{{ (c4() * currencies.USDPEN.value).toFixed(0) }}</td>
              <td>{{ (c4() * currencies.USDEUR.value).toFixed(1) }}</td>
            </tr>
            <tr>
              <td colspan="2"></td>
              <td class="text-center"><b>Gana</b></td>
              <td id="result-1st-table">
                <b>{{ d5().toFixed(1) }}&dollar;</b>
              </td>
              <td id="percent-1st-table">
                <b>{{ ((d5() * b5) / 100).toFixed(2) }}%</b>
              </td>
            </tr>
            <tr>
              <td class="bg-dark" colspan="5"></td>
            </tr>
            <tr>
              <td colspan="2"></td>
              <td>
                <div class="input-group">
                  <input
                    @click.self="clickHandler"
                    type="text"
                    class="text-center form-control form-control-sm"
                    v-model.lazy="c8"
                  />
                </div>
              </td>
              <td>
                <div class="input-group">
                  <input
                    @click.self="clickHandler"
                    type="text"
                    class="text-center form-control form-control-sm"
                    v-model.lazy="d8"
                  />
                </div>
              </td>
              <td>
                <div class="input-group">
                  <input
                    @click.self="clickHandler"
                    type="text"
                    class="text-center form-control form-control-sm"
                    v-model.lazy="e8"
                  />
                </div>
              </td>
            </tr>
            <tr>
              <td>#1</td>
              <td>{{ b3 }}</td>
              <td>{{ c9().toFixed(2) }}</td>
              <td>{{ d9().toFixed(2) }}</td>
              <td>{{ e9().toFixed(2) }}</td>
            </tr>
            <tr>
              <td>#2</td>
              <td>{{ b4 }}</td>
              <td>{{ c10().toFixed(2) }}</td>
              <td>{{ d10().toFixed(2) }}</td>
              <td>{{ e10().toFixed(2) }}</td>
            </tr>
            <tr>
              <td><b>Operaci&oacute;n</b></td>
              <td>{{ b11().toFixed(2) }} &dollar;</td>
              <td><b>Gana</b></td>
              <td id="result-2nd-table">
                <b>{{ d11().toFixed(1) }}$</b>
              </td>
              <td id="percent-2nd-table">
                <b>{{ (100 * (d11() / b11())).toFixed(2) }}%</b>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "calculator",
  props: ["currencies", "title"],
  data() {
    return {
      b5: 100,
      c8: 100,
      d8: 0,
      e8: 0,
      b3: "2.5",
      b4: "3",
    };
  },
  updated() {
    this.colorResult("result-1st-table");
    this.colorResult("percent-1st-table");
    this.colorResult("result-2nd-table");
    this.colorResult("percent-2nd-table");
  },
  methods: {
    colorResult(id) {
      let td = document.getElementById(id);
      td.className =
        td.innerText.search("-") >= 0
          ? "bg-danger text-white"
          : "bg-success text-white";
    },
    clickHandler() {
      event.target.select();
    },
    focusNextRate() {
      document.getElementById("rate-2").focus();
      document.getElementById("rate-2").select();
    },
    b11() {
      return this.c9() + this.c10();
    },
    c3() {
      return (
        this.b5 *
        (parseFloat(this.b4) / (parseFloat(this.b3) + parseFloat(this.b4)))
      );
    },
    c4() {
      return (
        this.b5 *
        (parseFloat(this.b3) / (parseFloat(this.b3) + parseFloat(this.b4)))
      );
    },
    c9() {
      if (this.d8 != "0" && this.d8 != "") {
        return this.d9() * this.currencies.PENUSD.value;
      } else {
        if (this.c8 != "0" && this.c8 != "") {
          return (
            parseFloat(this.c8) *
            (parseFloat(this.b4) /
              (parseFloat(this.b3) + parseFloat(this.b4)) /
              (1 -
                parseFloat(this.b4) /
                  (parseFloat(this.b3) + parseFloat(this.b4))))
          );
        } else {
          return this.e9() / this.currencies.USDEUR.value;
        }
      }
    },
    c10() {
      if (this.d8 != "0" && this.d8 != "") {
        return this.d10() * this.currencies.PENUSD.value;
      } else {
        if (this.c8 != "0" && this.c8 != "") {
          return parseFloat(this.c8);
        } else {
          return this.e10() / this.currencies.USDEUR.value;
        }
      }
    },
    d5() {
      return parseFloat(this.b3) * this.c3() - this.b5;
    },
    d9() {
      if (this.d8 != "0" && this.d8 != "") {
        return (
          parseFloat(this.d8) *
          (parseFloat(this.b4) /
            (parseFloat(this.b3) + parseFloat(this.b4)) /
            (1 -
              parseFloat(this.b4) /
                (parseFloat(this.b3) + parseFloat(this.b4))))
        );
      } else {
        return this.e9() / this.currencies.PENEUR.value;
      }
    },
    d10() {
      if (this.d8 != "0" && this.d8 != "") {
        return parseFloat(this.d8);
      } else {
        if (this.c8 != "0" && this.c8 != "")
          return this.c10() * this.currencies.USDPEN.value;
        return this.e10() / this.currencies.PENEUR.value;
      }
    },
    d11() {
      return parseFloat(this.b3) * this.c9() - this.b11();
    },
    e9() {
      if (this.d8 != "0" && this.d8 != "") {
        return this.c9() * this.currencies.USDEUR.value;
      } else {
        if (this.c8 == "0") {
          return (
            parseFloat(this.e8) *
            (parseFloat(this.b4) /
              (parseFloat(this.b3) + parseFloat(this.b4)) /
              (1 -
                parseFloat(this.b4) /
                  (parseFloat(this.b3) + parseFloat(this.b4))))
          );
        } else {
          return this.c9() * this.currencies.USDEUR.value;
        }
      }
    },
    e10() {
      if (this.d8 != "0" && this.d8 != "") {
        return this.c10() * this.currencies.USDEUR.value;
      } else {
        if (this.c8 != "0" && this.c8 != "") {
          return this.c10() * this.currencies.USDEUR.value;
        } else {
          return parseFloat(this.e8);
        }
      }
    },
  },
  components: {},
};
</script>

<style>
@media screen and (min-width: 600px) and (max-width: 800px) {
  * {
    font-size: 1.2rem !important;
    vertical-align: middle !important;
  }
  table {
    height: 80vh;
  }
}
</style>